import { Component, Input, OnInit } from '@angular/core';
import { async } from 'rxjs';
import { user } from 'src/app/shared/models/product';

import { CartService } from 'src/app/shared/services/cart.service';

@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.css'],
})
export class ProductComponent implements OnInit {

  public items: Array<user> = [{}];
  public item: user = {};
  public spacify: any = {};
  public id!: string;
  constructor(public appHttp:CartService) {}

  async ngOnInit() {
    try {
      this.items = await this.appHttp.getAll();
    } catch (error) {
      console.log(error);
    }
  }


  async AddItem() {
    debugger;
    let blogLocal: user = Object.assign({}, this.item);
    this.item = await this.appHttp.AddItem(blogLocal);
    this.items = await this.appHttp.getAll();
  }
    async DelItem(id:any) {

    this.spacify =  this.appHttp.DelItem(id);
    this.items = await this.appHttp.getAll();
  }

  async UpdItem(id:any) {
    this.item = await this.appHttp.UpdItem(id);
    this.item = await this.appHttp.DelItem(id);
    this.items = await this.appHttp.getAll();
  }

  }




